import { Paginator } from '@app/shared/models/paginator.model';
import { Router } from '@angular/router';
import { AdminStaffService } from '@app/core/services/admin-staff.service';
import { Component, OnInit } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
@Component({
  selector: 'app-list-staff',
  templateUrl: './list-staff.component.html',
  styleUrls: ['./list-staff.component.scss']
})
export class ListStaffComponent implements OnInit {
  listEmployees: any;
  cols: any[];
  vari: any;
  paginator:Paginator;
  constructor(private adminStaffService: AdminStaffService,
    private router: Router) { }

  ngOnInit() {
    this.cols = [
      { field: 'id', header: 'ID' },
      { field: 'first_name', header: 'First-Name' },
      { field: 'last_name', header: 'Last-Name' },
      { field: 'email', header: 'Email' },
      { field: 'department', header: 'Department' },
      { field: 'address', header: 'Address' },
      { field: 'district', header: 'District' },
      { field: 'state', header: 'State' }

    ];
    this.getEmployeeList();
    this.paginator= {
      limit:5,
      pageNo:0,
      totalCount:0,
      paginator:true,
      rows:5,
      loading:true
    }
  }
  editStaff(rowData: any) {
    this.router.navigate(['/edit-staff', rowData.id]);
  }
  getEmployeeList() {
    this.adminStaffService.getStaffList().subscribe(
      (data: HttpResponse<any>) => {
        this.listEmployees = data;
      },
      (err: any) => {

      }
    );
  }

  deleteRow(rowData: any) {
    this.adminStaffService.deleteStaff(rowData.id).subscribe(
      (success: any) => {
        swal('', 'Successfully Deleted', 'success');
        this.getEmployeeList();
      },
      (err: any) => {
      }
    );
  }
}
