import { LoginComponent } from '@app/feature/auth/login/login.component';
import { ListStaffComponent } from '@app/feature/admin/staff-management/components/list-staff/list-staff.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
const routes: Routes = [
  {path: 'list-staff', component: ListStaffComponent},
  {path: 'login', component: LoginComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SharedRoutingModule { }
