import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '@app/core/services/auth.service';
import { LoginService } from '@app/core/services/login.service';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { JwtHelperService } from '@auth0/angular-jwt';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
username: string;
pasword: string;
error: boolean;
currentUrl: string;
title: string;
  constructor(private loginService: LoginService,
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute
    ) { }

  ngOnInit() {

  }
  onSubmit(signInForm: NgForm) {
    this.loginService.login(signInForm.value).subscribe(
      (data: any) => {
        if (data.code === 401) {
          this.error = true;
        } else {
          const helper = new JwtHelperService();
          const decodedToken = helper.decodeToken(data.data.id_token);

          this.authService.setToken(data.data.id_token);
          this.authService.setRole(decodedToken.auth);

          this.authService.getAuthenticatedUserDetails().subscribe(
            (data: any) => {
              this.authService.setAuth(data['data']);
            },
            err => this.authService.purgeAuth()
          );
          // if (decodedToken.auth === 'ROLE_ADMIN') {
          //   this.router.navigateByUrl('/manage-supplier-list');
          // } else if (decodedToken.auth === 'ROLE_BUYER') {
          //   this.router.navigateByUrl('/home');
          // } else if (decodedToken.auth === 'ROLE_SUPPLIER') {
          //   this.router.navigateByUrl('manage-supplier-product');
          // }
        }
      },
      err => {}
    );
  }
}
