import { ListStudentsComponent } from '@app/feature/admin/students-management/components/list-students/list-students.component';
import { RemoveStudentComponent } from '@app/feature/admin/students-management/components/remove-student/remove-student.component';
import { EditStudentComponent } from '@app/feature/admin/students-management/components/edit-student/edit-student.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddStudentComponent } from '@app/feature/admin/students-management/components/add-student/add-student.component';
const routes: Routes = [
  {path: '', component: ListStudentsComponent},
  {path: 'add-student', component: AddStudentComponent},
  {path: 'edit-student', component: EditStudentComponent},
  {path: 'remove-student', component: RemoveStudentComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StudentsManagementRoutingModule { }
