import { User } from '@app/shared/models/user.model';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { AdminStaffService } from '@app/core/services/admin-staff.service';
import { HttpResponse } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { Staff} from '@app/shared/models/staff.model'


@Component({
  selector: 'app-edit-staff',
  templateUrl: './edit-staff.component.html',
  styleUrls: ['./edit-staff.component.scss']
})
export class EditStaffComponent implements OnInit {
  listEmployees: any;
  staffId:any;
  data:any;
  staffModel:Staff;
  first_name:any;
  last_name:any;
  department:any;
  address:any;
  district:any;
  state:any;
  staffDetails:any
  constructor(private adminStaffService: AdminStaffService,
    private route: ActivatedRoute,
    private router: Router,
    ) { 
      this.route.paramMap.subscribe(params => {
        this.staffId=params.get('id');
      });
     
    }

  ngOnInit() {
    this.selectedStaffDetails(this.staffId);
  }
  routeBack(){
    this.router.navigateByUrl('/list-staff');
  }
  onSubmit(form: NgForm) {
    this.data = form;
    this.adminStaffService.updateSupplierDetails(this.data.value, this.staffId).subscribe(
      (data: any) => {
        swal('', 'Successfully edited', 'success');
        this.router.navigateByUrl('/list-staff');
      },
      (err: any) => {
      }
    );
  } 
  selectedStaffDetails(id:any){
    this.adminStaffService.getSelectedStaffDetails(id).subscribe((data)=>
    {
      this.staffDetails=data;
    });
  }
}
