import { RemoveStaffComponent } from '@app/feature/admin/staff-management/components/remove-staff/remove-staff.component';
import { EditStaffComponent } from '@app/feature/admin/staff-management/components/edit-staff/edit-staff.component';
import { AddStaffComponent } from '@app/feature/admin/staff-management/components/add-staff/add-staff.component';
import { ListStaffComponent } from '@app/feature/admin/staff-management/components/list-staff/list-staff.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
const routes: Routes = [
  {path: '', component: ListStaffComponent},
  {path: 'add-staff', component: AddStaffComponent},
  {path: 'edit-staff/:id', component: EditStaffComponent},
  {path: 'remove-staff', component: RemoveStaffComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StaffManagementRoutingModule { }
